<template>
  <div class="login">
     <div class="swiper-img">
         <img :src="gettitle" alt="" />
     </div>
     <div class="swiper-head">
         <div class="top">
            login
         </div>
         <div class="top">
            register
         </div>
     </div>
     <div class="swiper-container detail"  >
        <div class="swiper-wrapper" >
          <transition name="fade1">
            <div class="swiper-slide" v-if="showLogin">
                <div class="bor1"></div>
                <div class="inp clearFix">
                    <img src="static/pic/person.png" alt="">
                    <input type="text" placeholder="username" v-model="user">
                </div>
                <div class="inp clearFix">
                   <img src="static/pic/password.png" alt="">
                   <input type="password" placeholder="password" v-model="password">
                </div>
                <button type="button" @click="login" class="btn-color1">Login</button>
                <button type="button" @click="toggle2" class="btn-color2">register</button>
            </div>
          </transition>
          <transition name="fade2">
            <div class="swiper-slide" v-if="showRegister">
                <div class="bor2"></div>
                <div class="inp">
                   <img src="static/pic/person.png" alt="">
                   <input type="text" placeholder="username" v-model="user1">
                </div>
                <div class="inp">
                   <img src="static/pic/password.png" alt="">
                   <input type="password" placeholder="password" v-model="password1">
                </div>
                <button type="button" @click="register" :class="style">Register</button>
            </div>
          </transition>
        </div>
     </div>
</div>
</template>
<style lang="stylus" rel="stylesheeet/stylus" scoped>
@import "../common/stylus/mixin.styl"
  .fade-enter-active {
    transition: all .5s ease
  }
  .fade-leave-active {
    transition: all 0s ease
  }
  .fade-enter, .fade-leave-active {
    transform: scale(0)
    opacity: 0
  }
.clearFix:before, .clearFix:after
      content: "";
      display: block;
      visibility: hidden;
      clear: both
.clearFix
     zoom: 1
.login
    width: 100%
    height: 17.786667rem
    .swiper-img
          width: 100%
          height: 2.666667rem
          text-align: center
          img
             width: 2rem
             height: 2.0rem
             margin-top: 0.333333rem
             border-radius: 50%
    .swiper-head
           width: 100%
           height: 1.333333rem
           display: flex
           .top
              flex: 1
              color: #26A2FF
              font-size: 0.48rem
              font-weight: bold
              text-align: center
              display: -webkit-box
              display: -ms-flexbox
              display: -webkit-flex
              display: flex
              -webkit-box-pack: center
              -ms-flex-pack: center
              -webkit-justify-content: center
              justify-content: center
              -webkit-box-align: center
              -ms-flex-align: center
              -webkit-align-items: center
              align-items: center
    .swiper-container
        width: 100%
        height: 13.786667rem
       .swiper-wrapper
         height: 13.786667rem
         width: 100%
         text-align: center
         .swiper-slide
           width: 100%
           height: 13.786667rem
           background-size: 100%,100%
           background-repeat: no-repeat
           .bor1
              width: 50%
              border: 1px solid #26A2FF
           .bor2
              width: 50%
              margin-left: 50%
              border: 1px solid #26A2FF
           .inp
              margin-top: 0.533333rem
              width: 80%
              display: flex
              height: 1.333333rem
              margin-left: 10%
              line-height: 1.333333rem
              img
                flex: 0 0 1rem
                width: 1rem
                height: 1rem
                margin-right: 0.133333rem
                margin-top: 0.2rem
              input
                font-size: 0.48rem
                flex: 1
                margin: 0 auto
                height: 1.333333rem
                border-bottom: 1px solid #26A2FF
                color: #26A2FF
                background: transparent
                outline:none;
            button
                background:transparent
                border-radius: 0.2rem
                width: 80%
                padding: 0.2rem 0.426667rem
                text-align: center
                text-decoration: none
                display: inline-block
                font-size: 0.48rem
                outline: none
            .btn-color1
                border: 1px solid #26A2FF
                color: #26A2FF
                margin-top: 1.333333rem
            .btn-color2
                border: 1px solid #AEAEAE
                color: #AEAEAE
                margin-top: 0.666667rem

</style>
<script type="text/ecmascript-6">
import io from 'socket.io-client'
import {mapGetters} from 'vuex'
import { Toast } from 'mint-ui'
  export default {
       data () {
          return {
             showLogin: true,
             showRegister: false,
             user: '',
             password: '',
             user1: '',
             password1: '',
             password2: '',
             startX:'',
             startT:'',
             isMove: false,
             direction:'left',
             style:''

          }
       },
       created () {
          var that  = this;
          this.$store.commit('setsocket',io.connect('http://60.205.181.234/restful'));
       },
       mounted () {
       },
       methods: {
           toggle2 () {
             this.showLogin = false;
             this.showRegister = true ;
             this.style = 'btn-color1';
           },
           login () {
                var that = this;
                var data = {
                  user:this.user,
                  password:this.password
                }
                this.$store.dispatch('tologin',data)
                     .then(() => {
                        setTimeout(() => {
                            this.$router.push({path:'/movie'});
                            this.getsocket.emit('login',data);
                        },200);
                     })
           },
           register () {
             this.$http.post('restful/register',{
                 user:this.user1,
                 password:this.password1,
                 src: ''
           }).then((response) => {
                 if(response.body.errno == 0){
                   this.showLogin = true;
                   this.showRegister = false ;
                   Toast('注册成功');
                 }else{
                   Toast({
                       message: '注册失败',
                       iconClass: 'icon icon-success'
                    });
                   return false;
                 }
           })
           },
           changeShow () {
              this.$store.commit('setshowTab');
           }
       },
       computed : {
         ...mapGetters([
              'getusername',
              'getshowDetail',
              'getusersrc',
              'getbgImg',
              'getsocket',
              'gettitle'
         ])
       }
  }
</script>



